import { Bread } from 'Bread'
import React, { PureComponent } from 'react'
import Form from './components/./Form'
import Table from './components/./Table'
import Tool from './components/./Tool'
import { connect } from 'react-redux'
import { mapDispatchToProps } from './redux/mapDispatchToProps'
@connect(
  state => ({
    ...state.bomSetting,
    menuPermissionIds: state.menuPermissionIds
  }),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor(props) {
    super(props)
  }
  componentDidMount() {}
  componentWillUnmount() {
    this.props.update({
      searchResult: [],
      searchParams: {},
      productCodeOptions: {}
    })
  }
  componentWillReceiveProps(nextProps) {}
  render() {
    const {
      menuPermissionIds
     } = this.props
    return (
      <section>
        <Bread route={['设置中心', '商品管理', 'BOM设置']} />
        <Form />
        <Tool menuPermissionIds={menuPermissionIds}/>
        <Table menuPermissionIds={menuPermissionIds}/>
      </section>
    )
  }
}
export default App



