import React, { PureComponent } from 'react'
import { message, Modal, Button, Dropdown, Menu } from 'antd'
import {
  API_BOMSETTING_IMPORT_COMPOSE,
  API_BOMSETTING_IMPORT_DECOMPOSE
} from 'api'
import style from './style/Tool.scss'
import { connect } from 'react-redux'
import { withRouter } from 'react-router'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
import { Import } from 'HOC'
import Debounce from 'lodash-decorators/debounce'
import { downloadTemplate } from 'util'
const confirm = Modal.confirm
@connect((state) => ({ ...state.bomSetting }), mapDispatchToProps)
class TableTool extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {}
  }
  // 新增
  handleAdd = () => {
    const { history } = this.props
    history.push(
      '/mms/settingCenter/productManage/bomSetting/create?editOrCreate=create&permissionFlag=allowEdit'
    )
  }
  // 删除
  handleOperator = () => {
    const {
      handleGetList,
      searchParams,
      page,
      size,
      selectedRowKeys,
      handleDel,
      update
    } = this.props
    if (!(selectedRowKeys && selectedRowKeys.length > 0)) {
      return message.error('没有可以操作的选项')
    }
    confirm({
      title: '温馨提示',
      content: '是否进行删除操作？',
      okText: '确认',
      width: 520,
      cancelText: '取消',
      onOk() {
        handleDel(
          {
            data: selectedRowKeys
          },
          () => {
            update({ selectedRowKeys: [] })
            message.success('操作成功')
            handleGetList({ searchParams: { page, size, ...searchParams } })
          }
        )
      }
    })
  }
  // 导出

  handleExport = () => {
    const { handleExport, searchParams, page, size } = this.props
    let reqObj = { ...searchParams }
    for (let i in reqObj) {
      if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
        reqObj[i] = reqObj[i].value
      }
    }
    handleExport({ searchParams: { page, size, ...reqObj } }, () => {
      message.success('导出成功')
    })
  }
  @Debounce(300)
  handleDownLoad = ({ key }) => {
    const downOpt = {
      1: 'T06',
      2: 'T07',
      3: 'T08'
    }
    const downKey = downOpt[key]
    downloadTemplate(downKey)
  }
  handleImportSuccess = (res) => {
    const { handleGetList, searchParams, page, size, update } = this.props
    res && res.message === 'ok' && message.success('导入成功')
    update({
      tableLoading: false,
      modalTuttonLoading_handleImport: false
    })
    handleGetList({ searchParams: { page, size, ...searchParams } })
  }

  /* 报错导出 */
  @Debounce(300)
  handleExprotError = (data) => {
    const { update } = this.props
    update({
      tableLoading: false,
      modalTuttonLoading_handleImport: false
    })
  }
  render() {
    const {
      modalTuttonLoading,
      menuPermissionIds = [],
      modalTuttonLoading_handleImport,
      modalTuttonLoading_handleExport
    } = this.props
    const menu = (
      <Menu>
        <Menu.Item key="1">
          {
            <Import
              multiple={false}
              action={{ api: API_BOMSETTING_IMPORT_COMPOSE }}
              onOk={this.handleImportSuccess}
              onExprotError={this.handleExprotError}
              beforeImport={() => {
                const { update } = this.props
                update({
                  tableLoading: true,
                  modalTuttonLoading_handleImport: true
                })
              }}
              onResponse={(data) => {}}
            >
              组合型
            </Import>
          }
        </Menu.Item>
        <Menu.Item key="2">
          {
            <Import
              multiple={false}
              action={{ api: API_BOMSETTING_IMPORT_DECOMPOSE }}
              onOk={this.handleImportSuccess}
              onExprotError={this.handleExprotError}
              beforeImport={() => {
                const { update } = this.props
                update({
                  tableLoading: true,
                  modalTuttonLoading_handleImport: true
                })
              }}
              onResponse={(data) => {}}
            >
              分解型
            </Import>
          }
        </Menu.Item>
        <Menu.Item key="3">
          {
            <Import
              multiple={false}
              action={{ api: API_BOMSETTING_IMPORT_COMPOSE }}
              onOk={this.handleImportSuccess}
              onExprotError={this.handleExprotError}
              beforeImport={() => {
                const { update } = this.props
                update({
                  tableLoading: true,
                  modalTuttonLoading_handleImport: true
                })
              }}
              onResponse={(data) => {}}
            >
              粗加工
            </Import>
          }
        </Menu.Item>
      </Menu>
    )
    const menuDownLoad = (
      <Menu onClick={this.handleDownLoad}>
        <Menu.Item key="1">组合型</Menu.Item>
        <Menu.Item key="2">分解型</Menu.Item>
        <Menu.Item key="3">粗加工</Menu.Item>
      </Menu>
    )
    return (
      <div className={style['table-tool']}>
        {menuPermissionIds && menuPermissionIds.includes('bomSetting_add') && (
          <Button type="primary" className={style.btn} onClick={this.handleAdd}>
            新增
          </Button>
        )}
        {menuPermissionIds && menuPermissionIds.includes('bomSetting_delete') && (
          <Button
            type="primary"
            className={style.btn}
            onClick={this.handleOperator}
          >
            删除
          </Button>
        )}
        <Dropdown overlay={menuDownLoad}>
          <Button
            type="primary"
            className={style.btn}
            icon="sync"
            disabled={modalTuttonLoading_handleExport}
          >
            下载模板
          </Button>
        </Dropdown>
        <Dropdown overlay={menu} overlayClassName={'dropDownUpload'}>
          <Button
            type="primary"
            icon="upload"
            className={style.btn}
            loading={modalTuttonLoading_handleImport}
            disabled={modalTuttonLoading_handleImport}
          >
            导入
          </Button>
        </Dropdown>

        {menuPermissionIds && menuPermissionIds.includes('bomSetting_export') && (
          <Button
            type="primary"
            className={style.btn}
            onClick={this.handleExport}
            loading={modalTuttonLoading}
          >
            导出
          </Button>
        )}
      </div>
    )
  }
}
export default withRouter(TableTool)



