import * as actionType from './actionType'
import { getData } from 'util'
import {
  API_BOMSETTINGS_PAGE,
  API_BOMSETTINGS_CREATE,
  API_BOMSETTINGS_UPDATE,
  API_BOMSETTINGS_DEL,
  API_BOMSETTINGS_EXPORT,
  API_BOMSETTINGS_OPTION
} from 'api'
//同步
export const update = object => ({
  type: actionType.BOMSETTING_UPDATE,
  data: object
})

//异步
// 获取列表
export const handleGetList = reqData => dispatch => {
  dispatch({
    type: actionType.BOMSETTING_UPDATE,
    data: { tableLoading: true }
  })
  const { searchParams = {} } = reqData
  for (let i in searchParams) {
    if (Object.prototype.toString.call(searchParams[i]) === '[object Object]') {
      searchParams[i] = searchParams[i].value
    }
  }
  getData({ api: API_BOMSETTINGS_PAGE, ...reqData })
    .then(data => {
      // 相应的逻辑判断
      let { page, size } = reqData.searchParams
      dispatch({
        type: actionType.BOMSETTING_UPDATE,
        data: {
          page,
          size,
          tableLoading: false,
          total: data.result.total,
          searchResult: data.result.list || []
        }
      })
    })
    .catch(err => {
      dispatch({
        type: actionType.BOMSETTING_FAIL
      })
    })
}
// 清空列表
export const cleanGetList = reqData => dispatch => {
  dispatch({
    type: actionType.BOMSETTING_UPDATE,
    data: {
      searchResult: []
    }
  })
}

// 创建/编辑仓库
export const handleAddOrEdit = (reqData, callBack) => dispatch => {
  dispatch({
    type: actionType.BOMSETTING_UPDATE,
    data: { modalTuttonLoading: true }
  })
  let api =
    reqData.data && reqData.data.id
      ? API_BOMSETTINGS_UPDATE
      : API_BOMSETTINGS_CREATE
  getData({ api, ...reqData })
    .then(data => {
      // 相应的逻辑判断
      dispatch({
        type: actionType.BOMSETTING_UPDATE,
        data: {
          modalTuttonLoading: false,
          isShowModal: false
        }
      })
      callBack && callBack()
    })
    .catch(err => {
      dispatch({
        type: actionType.BOMSETTING_FAIL
      })
    })
}
// 启用禁用BOM
export const handleOperator = (reqData, callBack) => dispatch => {
  getData({ api: API_BOMSETTINGS_OPTION, ...reqData })
    .then(data => {
      callBack && callBack(data)
    })
    .catch(err => {
      dispatch({
        type: actionType.BOMSETTING_DET_FAIL
      })
    })
}
// 删除BOM
export const handleDel = (reqData, callBack) => dispatch => {
  getData({ api: API_BOMSETTINGS_DEL, ...reqData })
    .then(data => {
      callBack && callBack(data)
    })
    .catch(err => {
      dispatch({
        type: actionType.BOMSETTING_DET_FAIL
      })
    })
}
// 导出
export const handleExport = (reqData, callBack) => dispatch => {
  dispatch({
    type: actionType.BOMSETTING_UPDATE,
    data: { modalTuttonLoading: true }
  })
  getData({ api: API_BOMSETTINGS_EXPORT, ...reqData })
    .then(data => {
      callBack && callBack()
      dispatch({
        type: actionType.BOMSETTING_UPDATE,
        data: { modalTuttonLoading: false }
      })
    })
    .catch(err => {
      dispatch({
        type: actionType.BOMSETTING_UPDATE,
        data: { modalTuttonLoading: false }

      })
    })
}



